import { useStore } from 'vuex';
import dayjs from 'dayjs';
export default {
  setup: function setup() {
    var weekList = ['S', 'M', 'T', 'W', 'T', 'F', 'S'];
    var store = useStore();
    var currentMonth = dayjs(store.state.nowTime).format('YYYY /  MM');
    return {
      currentMonth: currentMonth,
      weekList: weekList
    };
  }
};