import "core-js/modules/es.number.constructor.js";
import "core-js/modules/es.object.to-string.js";
import "core-js/modules/web.dom-collections.for-each.js";
import { ref, watch } from 'vue';
import { useStore } from 'vuex'; // hook

import { useMonth } from '../../hook/useMonth';
import dayjs from 'dayjs';
export default {
  props: {
    // 當前時間
    nowTime: {
      type: Number,
      required: true
    },
    nowMounth: {
      type: Boolean,
      defaults: false
    },
    currentSide: {
      type: Boolean,
      default: true // true -> front side ; false -> back side

    }
  },
  setup: function setup(props) {
    var store = useStore();
    var dateArr = ref([]);
    var failLogData = ref([]);
    var currentDate = dayjs().format('YYYY/M/D');
    failLogData.value = store.state.failLogData;
    useMonth(props.nowTime, dateArr.value, failLogData.value);

    if (props.nowMounth) {
      dateArr.value.forEach(function (item) {
        item.forEach(function (childItem) {
          if (childItem.timeStamp === currentDate) {
            store.commit('setCurrentTimeStamp', childItem.timeStamp);
            return;
          }
        });
      });
    } // click date


    var clickDate = function clickDate(tiemStamp) {
      store.commit('setCurrentSide', props.currentSide);
      store.commit('setCurrentTimeStamp', tiemStamp);
      store.commit('setCurrentStamp', tiemStamp);
    };

    watch(function () {
      return props.nowTime;
    }, function (newTimeStamp) {
      dateArr.value = [];
      useMonth(newTimeStamp, dateArr.value, failLogData.value);
    });
    return {
      dateArr: dateArr,
      failLogData: failLogData,
      clickDate: clickDate
    };
  }
};