import { useStore } from 'vuex';
import { useRouter } from 'vue-router'; // component

import MainHeader from './cpns/mainHeader.vue';
import WeekInfo from './cpns/weekInfo.vue';
import Calendar from '@/base-ui/calendar';
import dayjs from 'dayjs';
export default {
  components: {
    MainHeader: MainHeader,
    WeekInfo: WeekInfo,
    Calendar: Calendar
  },
  setup: function setup() {
    var store = useStore();
    var router = useRouter(); // init

    store.commit('setCurrentSide', true);
    store.commit('setCurrentStamp', dayjs().format('YYYY/MM/DD'));

    var goPespeakPage = function goPespeakPage() {
      router.push({
        path: '/pespeak',
        query: {
          stamp: store.state.currentTimeStamp
        }
      });
    };

    return {
      goPespeakPage: goPespeakPage
    };
  }
};